@use "@pkg/react-storybook/Component";
@use "@app/bancor-components/Style/Layout";
@use "@app/bancor-components/Style/Layout/Flex";
@use "@app/bancor-components/Style/Layout/Margin";
@use "@app/bancor-components/Style/Layout/Position";
@use "@app/bancor-components/Style/Color";
@use "@app/bancor-components/Style/Border";

$name: Image;
$type: Component.$type-element;
$selector: Component.selector($name, $type);

$height-logo-modo: 18px;
$width-logo-modo: 80px;

@mixin asModoBadge {
    @include Position.set($position: absolute, $z-index: alpha);
    @include Flex.set($align: "center", $justify: "center");
    @include Border.set($style: gamma, $width: beta, $color: "white", $radius: kappa );
    @include Color.set($bg: "white");
    box-sizing: border-box;
}

@mixin asModoLogo {
    @include Margin.set($left: alpha, $right: alpha);
    flex: 0 0 $width-logo-modo;
    svg {
        height: $height-logo-modo;
        width: $width-logo-modo;
    }
}

:export {
    NAME_IMAGE: $name;
    TYPE_IMAGE: $type;
}
