@use "@app/bancor-components/Element/Form/InputSelect";
@use "@app/bancor-components/Element/Form/Input";
@use "@app/bancor-components/Element/Form/InputNumber";
@use "@app/bancor-components/Element/Button";
@use "@app/bancor-components/Element/Form";
@use "@app/bancor-components/Element/Text";
@use "@app/bancor-components/Element/Image";
@use "@app/bancor-components/Style/Layout/Margin";
@use "@app/bancor-components/Style/Font";

.request {
    > header {
        .title {
            &#{Text.$selector} {
                @include Margin.set($bottom: beta);
                @include Font.set($size: iota, $weight: bold);
                word-break: break-word;
            }
        }
        & #{Image.$selector} {
            @include Image.asModoLogo;
        }
    }

    > section {
        .subtitle#{Text.$selector} {
            @include Margin.set($bottom: delta);
        }

        #{Button.$selector}.linkButton {
            @include Button.asUnderlineLink;
        }

        #{Form.$selector} {
            #{InputSelect.$selector},
            #{InputNumber.$selector},
            #{Input.$selector} {
                @include Margin.set($bottom: delta, $top: delta);
            }

            #{Button.$selector} {
                @include Margin.set($bottom: beta, $top: beta);
            }
        }
    }
}
